import React, {Dispatch, SetStateAction} from "react";
import s from "./Modal.module.css"
import image from "../../../assets/k.png"
import exit from "../../../assets/exit.png"

type ModalPropsType = {
    active: boolean;
    setModalActive: Dispatch<SetStateAction<boolean>>;
}
export const Modal = (props: ModalPropsType) => {
    return (<div className={props.active ? s.modalActive : s.modal} onClick={() => props.setModalActive(false)}>

            <div className={s.context}>
                <img  className={s.exit} src={exit} alt=""/>
                <div onClick={(event => event.stopPropagation())}>
                    <img src={image} alt="" className={s.icon}/>
                    <h2 className={s.h2}> ! <br/>   
                    </h2>
                    <p className={s.p}>           . 
                                
                        ,
                            .       
                        .</p>
                </div>

            </div>
        </div>
    );
}